<template>
    <div class="css3_wrap">
		<div id="container"></div>
		<div id="container2"></div>
		<div id="container3"></div>
		<div id="menu">
			<button id="table">TABLE</button>
			<button id="ring">RING</button>
			<button id="cone">CONE</button>
			<button id="sphere">SPHERE</button>
			<button id="helix">HELIX</button>
			<button id="grid">GRID</button>
			<button id="Torus">Torus</button>
		</div>
        <div class="back">
            <el-button @click="$router.go(-1)" type="primary">退 出</el-button>
        </div>
    </div>
</template>

<script>
import * as THREE from 'three';
import Event from '@/utils/object3DEvent'
import { TWEEN } from 'three/examples/jsm/libs/tween.module.min.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { CSS3DRenderer, CSS3DObject, CSS3DSprite } from 'three/examples/jsm/renderers/CSS3DRenderer.js';
import { CSS2DRenderer, CSS2DObject } from 'three/examples/jsm/renderers/CSS2DRenderer.js';
const vertexShader = `
		varying vec3 vPosition;
		varying vec2 vUv;
		void main() { 
			vUv = uv; 
			vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);
			gl_Position = projectionMatrix * mvPosition;
      vPosition = position;
		}
        `;
let fragmentShader = `
    uniform float iTime;
    uniform vec3 color;
    varying vec2 vUv;
    void main(void) {
        
        float d = 1.0 - mod(vUv.y * 1.0 - iTime, 1.0);

        gl_FragColor = vec4(color, pow(d, 5.0));
    }`
const table = [
    'H', 'H', '1.00794', 1, 1,
    'He', 'Helium', '4.002602', 18, 1,
    'Li', 'Lithium', '6.941', 1, 2,
    'Be', 'Beryllium', '9.012182', 2, 2,
    'B', 'Boron', '10.811', 13, 2,
    'C', 'Carbon', '12.0107', 14, 2,
    'N', 'Nitrogen', '14.0067', 15, 2,
    'O', 'Oxygen', '15.9994', 16, 2,
    'F', 'Fluorine', '18.9984032', 17, 2,
    'Ne', 'Neon', '20.1797', 18, 2,
    'Na', 'Sodium', '22.98976...', 1, 3,
    'Mg', 'Magnesium', '24.305', 2, 3,
    'Al', 'Aluminium', '26.9815386', 13, 3,
    'Si', 'Silicon', '28.0855', 14, 3,
    'P', 'Phosphorus', '30.973762', 15, 3,
    'S', 'Sulfur', '32.065', 16, 3,
    'Cl', 'Chlorine', '35.453', 17, 3,
    'Ar', 'Argon', '39.948', 18, 3,
    'K', 'Potassium', '39.948', 1, 4,
    'Ca', 'Calcium', '40.078', 2, 4,
    'Sc', 'Scandium', '44.955912', 3, 4,
    'Ti', 'Titanium', '47.867', 4, 4,
    'V', 'Vanadium', '50.9415', 5, 4,
    'Cr', 'Chromium', '51.9961', 6, 4,
    'Mn', 'Manganese', '54.938045', 7, 4,
    'Fe', 'Iron', '55.845', 8, 4,
    'Co', 'Cobalt', '58.933195', 9, 4,
    'Ni', 'Nickel', '58.6934', 10, 4,
    'Cu', 'Copper', '63.546', 11, 4,
    'Zn', 'Zinc', '65.38', 12, 4,
    'Ga', 'Gallium', '69.723', 13, 4,
    'Ge', 'Germanium', '72.63', 14, 4,
    'As', 'Arsenic', '74.9216', 15, 4,
    'Se', 'Selenium', '78.96', 16, 4,
    'Br', 'Bromine', '79.904', 17, 4,
    'Kr', 'Krypton', '83.798', 18, 4,
    'Rb', 'Rubidium', '85.4678', 1, 5,
    'Sr', 'Strontium', '87.62', 2, 5,
    'Y', 'Yttrium', '88.90585', 3, 5,
    'Zr', 'Zirconium', '91.224', 4, 5,
    'Nb', 'Niobium', '92.90628', 5, 5,
    'Mo', 'Molybdenum', '95.96', 6, 5,
    'Tc', 'Technetium', '(98)', 7, 5,
    'Ru', 'Ruthenium', '101.07', 8, 5,
    'Rh', 'Rhodium', '102.9055', 9, 5,
    'Pd', 'Palladium', '106.42', 10, 5,
    'Ag', 'Silver', '107.8682', 11, 5,
    'Cd', 'Cadmium', '112.411', 12, 5,
    'In', 'Indium', '114.818', 13, 5,
    'Sn', 'Tin', '118.71', 14, 5,
    'Sb', 'Antimony', '121.76', 15, 5,
    'Te', 'Tellurium', '127.6', 16, 5,
    'I', 'Iodine', '126.90447', 17, 5,
    'Xe', 'Xenon', '131.293', 18, 5,
    'Cs', 'Caesium', '132.9054', 1, 6,
    'Ba', 'Barium', '132.9054', 2, 6,
    'La', 'Lanthanum', '138.90547', 4, 9,
    'Ce', 'Cerium', '140.116', 5, 9,
    'Pr', 'Praseodymium', '140.90765', 6, 9,
    'Nd', 'Neodymium', '144.242', 7, 9,
    'Pm', 'Promethium', '(145)', 8, 9,
    'Sm', 'Samarium', '150.36', 9, 9,
    'Eu', 'Europium', '151.964', 10, 9,
    'Gd', 'Gadolinium', '157.25', 11, 9,
    'Tb', 'Terbium', '158.92535', 12, 9,
    'Dy', 'Dysprosium', '162.5', 13, 9,
    'Ho', 'Holmium', '164.93032', 14, 9,
    'Er', 'Erbium', '167.259', 15, 9,
    'Tm', 'Thulium', '168.93421', 16, 9,
    'Yb', 'Ytterbium', '173.054', 17, 9,
    'Lu', 'Lutetium', '174.9668', 18, 9,
    'Hf', 'Hafnium', '178.49', 4, 6,
    'Ta', 'Tantalum', '180.94788', 5, 6,
    'W', 'Tungsten', '183.84', 6, 6,
    'Re', 'Rhenium', '186.207', 7, 6,
    'Os', 'Osmium', '190.23', 8, 6,
    'Ir', 'Iridium', '192.217', 9, 6,
    'Pt', 'Platinum', '195.084', 10, 6,
    'Au', 'Gold', '196.966569', 11, 6,
    'Hg', 'Mercury', '200.59', 12, 6,
    'Tl', 'Thallium', '204.3833', 13, 6,
    'Pb', 'Lead', '207.2', 14, 6,
    'Bi', 'Bismuth', '208.9804', 15, 6,
    'Po', 'Polonium', '(209)', 16, 6,
    'At', 'Astatine', '(210)', 17, 6,
    'Rn', 'Radon', '(222)', 18, 6,
    'Fr', 'Francium', '(223)', 1, 7,
    'Ra', 'Radium', '(226)', 2, 7,
    'Ac', 'Actinium', '(227)', 4, 10,
    'Th', 'Thorium', '232.03806', 5, 10,
    'Pa', 'Protactinium', '231.0588', 6, 10,
    'U', 'Uranium', '238.02891', 7, 10,
    'Np', 'Neptunium', '(237)', 8, 10,
    'Pu', 'Plutonium', '(244)', 9, 10,
    'Am', 'Americium', '(243)', 10, 10,
    'Cm', 'Curium', '(247)', 11, 10,
    'Bk', 'Berkelium', '(247)', 12, 10,
    'Cf', 'Californium', '(251)', 13, 10,
    'Es', 'Einstenium', '(252)', 14, 10,
    'Fm', 'Fermium', '(257)', 15, 10,
    'Md', 'Mendelevium', '(258)', 16, 10,
    'No', 'Nobelium', '(259)', 17, 10,
    'Lr', 'Lawrencium', '(262)', 18, 10,
    'Rf', 'Rutherfordium', '(267)', 4, 7,
    'Db', 'Dubnium', '(268)', 5, 7,
    'Sg', 'Seaborgium', '(271)', 6, 7,
    'Bh', 'Bohrium', '(272)', 7, 7,
    'Hs', 'Hassium', '(270)', 8, 7,
    'Mt', 'Meitnerium', '(276)', 9, 7,
    'Ds', 'Darmstadium', '(281)', 10, 7,
    'Rg', 'Roentgenium', '(280)', 11, 7,
    'Cn', 'Copernicium', '(285)', 12, 7,
    'Nh', 'Nihonium', '(286)', 13, 7,
    'Fl', 'Flerovium', '(289)', 14, 7,
    'Mc', 'Moscovium', '(290)', 15, 7,
    'Lv', 'Livermorium', '(293)', 16, 7,
    'Ts', 'Tennessine', '(294)', 17, 7,
    'Og', 'Oganesson', '(294)', 18, 7
];
let camera, scene, renderer, renderer2,renderer3, clock = new THREE.Clock();
let controls;
const objects = [];
const targets = {
    Torus: [], 
    ring: [],
    cone: [], 
    table: [], 
    sphere: [], 
    helix: [], 
    grid: [] 
}
export default {
    methods: {
        data() {
            return {
                animateId: null
            }
        },
        init() {  
            camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 10000 );
            camera.position.z = 5000;
            camera.position.y = 3000;
            scene = new THREE.Scene();
            new Event(window, camera)
            var DirectionalLight = new THREE.DirectionalLight( 0xffffff);
            DirectionalLight.position.set( 0, 50, 0 );
            scene.add( DirectionalLight );

            let AmbientLight = new THREE.AmbientLight( 0xffffff );
            scene.add( AmbientLight );
            //
            renderer = new THREE.WebGLRenderer({ alpha: true });
            renderer.setSize( window.innerWidth, window.innerHeight );
            document.getElementById( 'container' ).appendChild( renderer.domElement );

            renderer2 = new CSS3DRenderer({ alpha: true });
            renderer2.setSize( window.innerWidth, window.innerHeight );
            document.getElementById( 'container2' ).appendChild( renderer2.domElement );
            //
            renderer3 = new CSS2DRenderer({ alpha: true });
            renderer3.setSize( window.innerWidth, window.innerHeight );
            document.getElementById( 'container3' ).appendChild( renderer3.domElement );
            //
            controls = new OrbitControls( camera, renderer3.domElement );
            controls.minDistance = 500;
            controls.maxDistance = 10000;
            controls.enableDamping = true

            this.createPointData()
            this.createBox()
        },
        createPointData() {
             // table
            for ( let i = 0; i < table.length; i += 5 ) {
                const element = document.createElement( 'div' );
                element.className = 'element';
                element.style.backgroundColor = 'rgba(0,127,127,' + ( Math.random() * 0.5 + 0.25 ) + ')';

                const number = document.createElement( 'div' );
                number.className = 'number';
                number.textContent = ( i / 5 ) + 1;
                element.appendChild( number );

                const symbol = document.createElement( 'div' );
                symbol.className = 'symbol';
                symbol.textContent = table[ i ];
                element.appendChild( symbol );

                const details = document.createElement( 'div' );
                details.className = 'details';
                details.innerHTML = table[ i + 1 ] + '<br>' + table[ i + 2 ];
                element.appendChild( details );
                document.body.appendChild(element)
                const objectCSS = new CSS3DSprite( element );
                objectCSS.position.x = Math.random() * 4000 - 2000;
                objectCSS.position.y = Math.random() * 4000 - 2000;
                objectCSS.position.z = Math.random() * 4000 - 2000;
                scene.add( objectCSS );

                objects.push( objectCSS );

                const object = new THREE.Object3D();
                object.position.x = ( table[ i + 3 ] * 140 ) - 1330;
                object.position.y = - ( table[ i + 4 ] * 180 ) + 990;

                targets.table.push( object );

            }
            // sphere

            const vector = new THREE.Vector3();

            let ring = new THREE.RingGeometry(1400,1200, 58)
            let vex = ring.attributes.position.array
            let itemSize = ring.attributes.position.itemSize
            // ring
            for ( let i = 0; i < objects.length; i += 1 ) {

                const object = new THREE.Object3D();
                let count = i*itemSize
                object.position.x = vex[count];
                object.position.y = vex[count+1];
                object.position.z = vex[count+2];

                // vector.copy( camera.position ).multiplyScalar( 2 );
                // object.lookAt( vector );

                targets.ring.push( object );
            }

            let cone = new THREE.ConeGeometry(2000, 3000, 16, 16)
            let coneVex = cone.attributes.position.array
            let coneSize = cone.attributes.position.itemSize
            // cone
            for ( let i = 0; i < objects.length; i += 1 ) {

                const object = new THREE.Object3D();
                let count = i*coneSize
                object.position.x = coneVex[count];
                object.position.y = coneVex[count+1] - 600;
                object.position.z = coneVex[count+2];

                vector.copy( object.position ).multiplyScalar( 2 );
                object.lookAt( 0,0,0 );

                targets.cone.push( object );
            }

            let Torus = new THREE.TorusGeometry(1200, 200, 10, 10)
            let TorusVex = Torus.attributes.position.array
            let TorusSize = Torus.attributes.position.itemSize
            // Torus
            for ( let i = 0; i < objects.length; i += 1 ) {

                const object = new THREE.Object3D();
                let count = i*TorusSize
                object.position.x = TorusVex[count];
                object.position.y = TorusVex[count+1];
                object.position.z = TorusVex[count+2];

                vector.copy( object.position ).multiplyScalar( 2 );
                object.lookAt( 0,0,0 );

                targets.Torus.push( object );
            }

            // 
            for ( let i = 0, l = objects.length; i < l; i ++ ) {

                const phi = Math.acos( - 1 + ( 2 * i ) / l );
                const theta = Math.sqrt( l * Math.PI ) * phi;

                const object = new THREE.Object3D();

                object.position.setFromSphericalCoords( 800, phi, theta );

                vector.copy( object.position ).multiplyScalar( 2 );

                object.lookAt( vector );

                targets.sphere.push( object );

            }

            // // helix

            for ( let i = 0, l = objects.length; i < l; i ++ ) {

                const theta = i * 0.175 + Math.PI;
                const y = - ( i * 8 ) + 450;

                const object = new THREE.Object3D();

                object.position.setFromCylindricalCoords( 900, theta, y );

                vector.x = object.position.x * 2;
                vector.y = object.position.y;
                vector.z = object.position.z * 2;

                object.lookAt( vector );

                targets.helix.push( object );

            }

            // // grid

            for ( let i = 0; i < objects.length; i ++ ) {

                const object = new THREE.Object3D();

                object.position.x = ( ( i % 5 ) * 400 ) - 800;
                object.position.y = ( - ( Math.floor( i / 5 ) % 5 ) * 400 ) + 800;
                object.position.z = ( Math.floor( i / 25 ) ) * 1000 - 2000;

                targets.grid.push( object );

            }
            

            const buttonTable = document.getElementById( 'table' );
            buttonTable.addEventListener( 'click', () => {

                this.transform( targets.table, 2000 );

            } );

            const buttonRing = document.getElementById( 'ring' );
            buttonRing.addEventListener( 'click', () => {

                this.transform( targets.ring, 2000 );

            } );

            const buttonCone = document.getElementById( 'cone' );
            buttonCone.addEventListener( 'click', () => {

                this.transform( targets.cone, 2000 );

            } );

            const buttonSphere = document.getElementById( 'sphere' );
            buttonSphere.addEventListener( 'click', () => {

                this.transform( targets.sphere, 2000 );

            } );

            const buttonHelix = document.getElementById( 'helix' );
            buttonHelix.addEventListener( 'click', () => {

                this.transform( targets.helix, 2000 );

            } );

            const buttonGrid = document.getElementById( 'grid' );
            buttonGrid.addEventListener( 'click', () => {

                this.transform( targets.grid, 2000 );

            } );

            const buttonTorus = document.getElementById( 'Torus' );
            buttonTorus.addEventListener( 'click', () => {

                this.transform( targets.Torus, 2000 );

            } );
            this.transform( targets.ring, 1000 );
        },
        createBox() {
           const box = new THREE.SphereGeometry(1500, 64, 64)
           const mesh = new THREE.Mesh(box, new THREE.ShaderMaterial({ 
                uniforms: {
                    iTime: { value: 0 },
                    color: { value: new THREE.Color(0xff0000)},
                    iResolution: {
                        value: new THREE.Vector2(800, 800)
                    }
                },
                side: 2,
                depthWrite: false,
                transparent: true,
                vertexShader,
                fragmentShader
            })) 
            scene.add(mesh)

            const element = document.createElement('div')
            element.className = 'info_text'

            const img = document.createElement('img')
            img.className = 'img'
            img.src = require('../../assets/zg.jpg')
            element.appendChild(img)

            const text = document.createElement('div')
            text.className = 'text'
            text.innerText = '中华人民共和国万岁'
            element.appendChild(text)
            
            const text2 = document.createElement('div')
            text2.className = 'text2'
            text2.innerText = '中国人民万岁'
            element.appendChild(text2)

            const obj = new CSS2DObject(element)

            obj.position.set(0, -800, 0)

            scene.add(obj)

            const obj2 = new CSS3DObject(element.cloneNode(true))

            obj2.position.set(0, 0, 0)
            
            scene.add(obj2)

        },
        transform( targets, duration ) {
            TWEEN.removeAll();

            for ( let i = 0; i < objects.length; i ++ ) {

                const object = objects[ i ];
                const target = targets[ i ];

                new TWEEN.Tween( object.position )
                    .to( { x: target.position.x, y: target.position.y, z: target.position.z }, Math.random() * duration + duration )
                    .easing( TWEEN.Easing.Exponential.InOut )
                    .start();

                new TWEEN.Tween( object.rotation )
                    .to( { x: target.rotation.x, y: target.rotation.y, z: target.rotation.z }, Math.random() * duration + duration )
                    .easing( TWEEN.Easing.Exponential.InOut )
                    .start();
            }
            new TWEEN.Tween( this )
                .to( {}, duration * 2 )
                .onUpdate( this.render )
                .start();

        },
        onWindowResize() {

            camera.aspect = window.innerWidth / window.innerHeight;
            camera.updateProjectionMatrix();

            renderer.setSize( window.innerWidth, window.innerHeight );
            renderer2.setSize( window.innerWidth, window.innerHeight );
            renderer3.setSize( window.innerWidth, window.innerHeight );

            this.render();

        },
        animate() {

            this.animateId = requestAnimationFrame( this.animate );

            TWEEN.update();

            controls.update();
            let delta = clock.getDelta()
            const elapsedTime = clock.getElapsedTime()
            // Update passes
            // scene.traverse((child) => {
            //     if (child.material){
            //         child.material.uniforms.iTime.value = elapsedTime
            //     }
            // })

            this.render()

        },
        render() {

            renderer.render( scene, camera );
            renderer2.render( scene, camera );
            renderer3.render( scene, camera );

        }
    },
    mounted() {
        this.init();
        this.animate();
        window.onresize = this.onWindowResize
    },
    destroyed() {
        scene.clear()
        cancelAnimationFrame(this.animateId)
        renderer.dispose()
    }
}
</script>

<style lang="scss">
.css3_wrap {
    width: 100%;
    height: 100vh;
    background: #000000;
    #menu{
        position: absolute;
        bottom: 20px;
        width: 100%;
        text-align: center;
        z-index: 50;
    }
    #container, #container2, #container3{
        background: rgba(0,0,0,0);
        position: absolute;
        left: 0;
        top: 0;
        z-index: 10;
    }
    #container2 {
        background: rgba(0,0,0,0);
        z-index: 20;
    }
    #container3 {
        background: rgba(0,0,0,0);
        z-index: 30;
        overflow: hidden;
    }
    .element {
        width: 120px;
        height: 160px;
        box-shadow: 0px 0px 12px rgba(0,255,255,0.5);
        border: 1px solid rgba(127,255,255,0.25);
        font-family: Helvetica, sans-serif;
        text-align: center;
        line-height: normal;
        cursor: default;
    }

    .element:hover {
        box-shadow: 0px 0px 12px rgba(238, 13, 43, 0.75);
        border: 1px solid rgba(127,255,255,0.75);
    }

    .element .number {
        position: absolute;
        top: 20px;
        right: 20px;
        font-size: 12px;
        color: rgba(127,255,255,0.75);
    }

    .element .symbol {
        position: absolute;
        top: 40px;
        left: 0px;
        right: 0px;
        font-size: 60px;
        font-weight: bold;
        color: orangered;
        text-shadow: 0 0 10px rgba(0,255,255,0.95);
        animation: light 1s linear infinite;
    }

    .element .details {
        position: absolute;
        bottom: 15px;
        left: 0px;
        right: 0px;
        font-size: 12px;
        color: rgba(127,255,255,0.75);
    }

    button {
        color: rgba(127,255,255,0.75);
        background: transparent;
        outline: 1px solid rgba(127,255,255,0.75);
        border: 0px;
        padding: 5px 10px;
        cursor: pointer;
    }

    button:hover {
        background-color: rgba(0,255,255,0.5);
    }

    button:active {
        color: #000000;
        background-color: rgba(0,255,255,0.75);
    }  

    @keyframes light {
        from {
            color: orchid;
        }
        to {
            color: red;
        }
    }

    .info_text {
        border-radius: 10px;
        padding: 10px;
        font-size: 20px;
        color: red;
        box-shadow: 0 0 50px 12px inset #41dddd;
        box-shadow: 0 0 50px 12px #41dddd;
        border: 2px solid #000;
        user-select: auto !important;
        pointer-events: auto;
    }
    .text {
        text-shadow: 0 0 10px 2px #41dddd;
    }
    .text2 {
        margin-top: 30px;
        font-size: 20px;
    }
    .img {
        width: 50px;
        height: 50px;
        pointer-events: none;
    }
    .back {
    position: absolute;
    bottom:  60px;
    right: 50%;
    z-index: 99;
    transform: translateX(50%);
    display: flex;
    }
}

</style>